async function insertOrder(info) {
  let params = {
    info,
  };
  let data = {};
  await fetch(`https://cors-anywhere.herokuapp.com/http://37.148.212.44:8080/insertorder`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      params,
    }),
  }).then(response => response.json()).then((responseJson) => {
    data = responseJson;
    console.log('data', data);
  },
      ).catch((err) => {
        console.log('Fetch Err', err);
      });
  return data;
}

